import React from 'react'
import TagList from '../components/TagList/TagList'
import { TagData } from '../components/TagList/TagData'

function Home() {
	return (
		<div className='home'>
			  	<div className='intro-container content'>
					<div className='intro-desc'>
						<h2>Hi there!</h2><br/>
					  	<p> I'm Anna, a fresh graduate from the University of California, Irvine majoring in Computer Science. My main field of interest is web development. Currently I'm self-learning frontend development. I built this site to apply new things I pick up about web development here and there as well as some previous projects I've worked on. I add some personal comments and notes along with each project's description as well. Thanks for visiting!</p>
					</div>

					<div className='intro-img'>
					  <img src={"https://minters.carrd.co/assets/images/gallery01/0d6a8947.jpg?v=2a01b3d9"} alt={"Me but not me"} />
					</div>

					<div className='intro-skills'>
						<TagList tags={Object.keys(TagData)}/>
					</div>
			  	</div>
    	</div>
	)
}

export default Home
